"use client";
import { useState } from "react";
import { motion } from "framer-motion";
import Tilt from "react-parallax-tilt";
import Image from "next/image";
import ProjectModal from "./ProjectModal";

const projects = [
  {
    id: "p1",
    title: "Sistema de pagos",
    description: "Aplicaci√≥n web para gesti√≥n de pagos, creada con Laravel y Vue.",
    image: "/images/proyecto1.jpg",
    demo: "https://example.com/demo1",
    repo: "https://github.com/your/repo1",
    tech: ["Laravel", "MySQL", "Vue"],
  },
  {
    id: "p2",
    title: "Portafolio interactivo",
    description: "Landing page moderna con animaciones 3D y dise√±o minimalista.",
    image: "/images/proyecto2.jpg",
    demo: "https://example.com/demo2",
    repo: "https://github.com/your/repo2",
    tech: ["Next.js", "Tailwind", "Framer Motion"],
  },
];

export default function ProjectsSection() {
  const [openProject, setOpenProject] = useState<any | null>(null);
  const [modalOpen, setModalOpen] = useState(false);

  const openModal = (proj: any) => {
    setOpenProject(proj);
    setModalOpen(true);
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  return (
<section id="proyectos" className="pt-1 pb-32 scroll-mt-34">

<h2
  className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-cyan-400 to-purple-500 md:text-4xl font-bold text-center mb-12"
>        Proyectos
      </h2>

      <div className="grid md:grid-cols-2 gap-8">
        {projects.map((p) => (
          <Tilt
            key={p.id}
            tiltMaxAngleX={8}
            tiltMaxAngleY={8}
            glareEnable={true}
            glareMaxOpacity={0.12}
            scale={1.02}
          >
            {/* group = permite hover sobre todo el div */}
            <motion.div
              className="group relative rounded-2xl overflow-hidden bg-white/80 dark:bg-gray-900/60 border border-gray-200 dark:border-gray-800 shadow-xl cursor-pointer"
              onClick={() => openModal(p)}
            >
              {/* Contenedor de imagen y overlay */}
              <div className="relative overflow-hidden rounded-2xl">
                <Image
                  src={p.image}
                  alt={p.title}
                  width={1200}
                  height={700}
                  className="w-full h-64 object-cover transition-transform duration-700 group-hover:scale-105"
                />

                {/* Overlay que cubre toda la imagen */}
                <div className="absolute inset-0 z-10 rounded-2xl bg-black/40 backdrop-blur-sm flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-500">
                  <motion.div
                    initial={{ scale: 0.9, opacity: 0 }}
                    whileInView={{ scale: 1, opacity: 1 }}
                    transition={{ duration: 0.3 }}
                    className="flex gap-3"
                  >
                    {/* Bot√≥n Live Demo */}
                    <a
                      href={p.demo}
                      target="_blank"
                      rel="noreferrer"
                      onClick={(e) => e.stopPropagation()}
                      className="px-4 py-2 bg-white/90 text-gray-800 rounded-full font-semibold shadow pointer-events-auto 
                                 transition-all duration-300 hover:scale-105 hover:shadow-[0_0_15px_rgba(255,255,255,0.7)]"
                    >
                      Live Demo
                    </a>

                    {/* Bot√≥n GitHub */}
                    <a
                      href={p.repo}
                      target="_blank"
                      rel="noreferrer"
                      onClick={(e) => e.stopPropagation()}
                      className="px-4 py-2 border border-white/70 text-white rounded-full font-semibold pointer-events-auto 
                                 transition-all duration-300 hover:bg-white/20 hover:border-white hover:scale-105 hover:shadow-[0_0_15px_rgba(255,255,255,0.5)]"
                    >
                      GitHub
                    </a>
                  </motion.div>
                </div>
              </div>

              {/* Texto del proyecto */}
              <div className="p-6">
                <h3 className="text-2xl font-semibold text-blue-600 mb-2">
                  {p.title}
                </h3>
                <p className="text-gray-600 dark:text-gray-300 mb-4">
                  {p.description}
                </p>
                <div className="flex gap-2 flex-wrap">
                  {p.tech.map((t) => (
                    <span
                      key={t}
                      className="bg-blue-100 text-blue-600 text-xs px-3 py-1 rounded-full"
                    >
                      {t}
                    </span>
                  ))}
                </div>
              </div>
            </motion.div>
          </Tilt>
        ))}
      </div>

      {/* Modal */}
      <ProjectModal
        project={openProject}
        open={modalOpen}
        onClose={() => setModalOpen(false)}
      />
    </section>
  );
}
