"use client";
import { motion } from "framer-motion";
import {
  SiNextdotjs, SiLaravel, SiReact, SiTailwindcss,
  SiMysql, SiPython, SiPhp, SiNodedotjs,
  SiGit, SiGithub, SiGitlab, SiFigma, SiVscodium
} from "react-icons/si";
import { FaDatabase, FaCode, FaProjectDiagram } from "react-icons/fa";

const categories = [
  {
    title: "Frontend",
    items: [
      { name: "Next.js", icon: SiNextdotjs, color: "#000000" },
      { name: "React", icon: SiReact, color: "#61DAFB" },
      { name: "TailwindCSS", icon: SiTailwindcss, color: "#38BDF8" },
    ],
  },
  {
    title: "Backend",
    items: [
      { name: "Laravel", icon: SiLaravel, color: "#FF2D20" },
      { name: "Node.js", icon: SiNodedotjs, color: "#3C873A" },
      { name: "Python", icon: SiPython, color: "#3776AB" },
      { name: "PHP", icon: SiPhp, color: "#8892BF" },
      { name: "MySQL", icon: SiMysql, color: "#00618A" },
      { name: "SQL Server", icon: FaDatabase, color: "#CC2927" },
    ],
  },
  {
    title: "Herramientas",
    items: [
      { name: "Git", icon: SiGit, color: "#F05033" },
      { name: "GitHub", icon: SiGithub, color: "#171515" },
      { name: "GitLab", icon: SiGitlab, color: "#FC6D26" },
      { name: "VS Code", icon: SiVscodium, color: "#007ACC" },
      { name: "Visual Studio", icon: FaCode, color: "#5C2D91" },
    ],
  },
  {
    title: "Dise√±o y Modelado",
    items: [
      { name: "Figma", icon: SiFigma, color: "#F24E1E" },
      { name: "Visual Paradigm", icon: FaProjectDiagram, color: "#1E90FF" },
    ],
  },
];

export default function TechGrid() {
  return (
    <section
      id="tecnologias"
      className="py-24 min-h-screen max-w-6xl mx-auto px-6 relative text-white"
    >
      {/* ‚úÖ Eliminamos el fondo transl√∫cido anterior */}

      <motion.h2
        initial={{ opacity: 0, y: 30 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
        viewport={{ once: true }}
        className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-cyan-400 to-purple-500 md:text-4xl font-bold text-center mb-12"
      >
        Tecnolog√≠as
   
      </motion.h2>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8 relative z-10">
        {categories.map((cat, i) => (
          <motion.div
            key={cat.title}
            initial={{ opacity: 0, y: 40 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: i * 0.1 }}
            viewport={{ once: true }}
            className="bg-white/10 backdrop-blur-sm 
                       border border-white/10 
                       rounded-2xl shadow-lg p-6 
                       transition-transform duration-300 
                       hover:-translate-y-2 hover:shadow-xl"
          >
            <h3 className="text-lg font-semibold text-center mb-5 
                           bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-500">
              {cat.title}
            </h3>

            <div className="grid grid-cols-2 gap-4 justify-center">
              {cat.items.map(({ name, icon: Icon, color }) => (
                <div
                  key={name}
                  className="flex flex-col items-center text-center gap-2"
                >
                  <div
                    className="p-2 rounded-lg shadow-sm"
                    style={{ backgroundColor: color }}
                  >
                    <Icon size={22} color="white" />
                  </div>
                  <span className="text-xs font-medium text-gray-200">
                    {name}
                  </span>
                </div>
              ))}
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
