"use client";
import { motion } from "framer-motion";

export default function HeroSection() {
  return (
    <section
      id="inicio"
      className="relative flex flex-col justify-center items-center min-h-screen overflow-hidden text-white"
    >
      {/* Fondo de part√≠culas (debe estar en z-[-10] o menos) */}
      <div className="absolute inset-0 -z-10">
        <canvas id="particle-bg" className="w-full h-full" />
      </div>

      {/* Contenido principal */}
      <motion.div
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1 }}
        className="text-center z-10"
      >
        <p className="tracking-[0.3em] text-sm text-gray-300 mb-3">
          PATRICIO MARDONES
        </p>

        <h1 className="text-5xl md:text-7xl font-extrabold mb-4">
          Software{" "}
          <span className="bg-gradient-to-r from-cyan-400 via-blue-500 to-purple-500 text-transparent bg-clip-text">
            Engineer
          </span>
        </h1>

        <p className="text-gray-300 text-lg md:text-xl max-w-2xl mx-auto leading-relaxed">
          Transformo ideas en experiencias digitales fluidas y elegantes con{" "}
          <span className="text-cyan-400">Next.js</span>,{" "}
          <span className="text-blue-400">Laravel</span> y{" "}
          <span className="text-purple-400">dise√±o interactivo</span>.
        </p>

        <motion.a
          href="#proyectos"
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
          className="inline-block mt-10 px-8 py-3 rounded-full border border-cyan-400 text-cyan-400 font-medium hover:bg-cyan-400 hover:text-black transition-colors duration-300"
        >
          Ver proyectos
        </motion.a>
      </motion.div>
    </section>
  );
}
